___CSS_LOADER_EXPORT___.push([module.id, `/* TabBar.css */

.molecule-tab-bar {
  display: flex;
  background-color: #f0f0f0;
  overflow: hidden; /* 넘치는 부분 감추기 */
}

.atom-tab {
  padding: 10px;
  margin: 5px;
  text-decoration: none;
  color: #333;
  border-radius: 5px;
  transition: background-color 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.atom-tab:hover {
  background-color: #ddd;
}

.atom-tab.active {
  background-color: #007bff;
  color: #fff;
  transform: translateY(-3px); /* 위로 약간 이동하는 효과 */
}
